import React from 'react';

class SearchResult extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      url: props.url,
      image: props.image,
      title: props.title,
      description: props.description,
    }
  }

  render () {
    return(
      <a className="article-container" href={this.state.url}>
        <style jsx>
          {`
            .article-container {
              margin: 5px;
              display:flex;
              flex-direction: column;
              text-decoration: none;
              color: black;
            }
            .article-container:hover {
              text-decoration: underline;
              color: rgb(163,163,163);
            }
            .article-title {
              font-size: 20px;
            }
            .article-description {
              font-size: 14px;
            }
            .article-image-container {
              max-width: 75px;
              max-height: 75px;
            }
            .article-image {
              object-fit: cover;
            }
          `}
        </style>
        <div className="article-image-container">
          <img className="article-image" src={this.state.image}></img>
        </div>
        <div className="article-title">{this.state.title}</div>
        <div className="article-desc">{this.state.description}</div>
      </a>
    )
  }
}

export default SearchResult;



